import React from 'react'
import useUser from '../hook/useUser'
import SystemInfo from '../components/system/SystemInfo'
import SystemConnect from '../components/system/SystemConnect'
import SystemTime from '../components/system/SystemTime'
import { useForm } from 'react-hook-form'
import axios from 'axios'
import Loading from '../components/Loading'

function System() {
    const isUser = useUser()
    const { username, user } = isUser ? JSON.parse(isUser) : null
    const { register, handleSubmit, setValue } = useForm()

    const [isLoading, setIsLoading] = React.useState(false)

    const onSubmit = async (input, e) => {
        e.target.querySelector('.btn-upload').disabled = true
        setIsLoading(true)
        const response = await axios.post(process.env.REACT_APP_CREATE, {
            ...input
        })
        console.log(response);
        const port = response.data.port
        setTimeout(() => {
            setIsLoading(false)
            window.open(`http://120.126.17.200:${port}`, '_blank');
        }, 10000)
        console.log('onSubmit');

    }

    const onError = (error) => {
        console.log('onError', error)
    }

    return (
        <form className="system" onSubmit={handleSubmit(onSubmit, onError)}>
            { isLoading ? <Loading /> : ''}
            <p className="user container-xl">{username}{user}</p>
            <div className="container-xl">
                <div className="row">
                    <div className="col-lg-4 col-md-6">
                        <div className="col-12 mb-4">
                            <div className="bg-primary rounded-top h-10"></div>
                            <div className="list border border-top-0 border-1 rounded-bottom py-3 px-3">
                                <div className="system-info">
                                    <SystemInfo register={register}/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col-md-6">
                        <div className="col-12 mb-4">
                            <div className="bg-secondary rounded-top h-10"></div>
                            <div className="list border border-top-0 border-1 rounded-bottom py-3 px-3">
                                <div className="connect">
                                    <SystemConnect register={register}/>
                                </div>
                            </div>
                        </div>
                        <div className="col-12 mb-4">
                            <div className="bg-green rounded-top h-10"></div>
                            <div className="list border border-top-0 border-1 rounded-bottom py-3 px-3">
                                <div className="execute-time">
                                    <SystemTime register={register}/>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-4 col">
                        <div className="right-section">
                            <div>
                                <iframe frameBorder="0" allowFullScreen="5" className="tutor-video" width="300" height="170" src="https://www.youtube.com/embed/SQPtkT3NbHs" title="YouTube video player" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"></iframe>
                            </div>
                            <div className="system-example">
                                <h4>套用範例</h4>
                                <div className="select-part">
                                    <h5 className="fs-6">
                                        如果您不知道該怎麼設定，根據不同用途， 
                                        Axtasy有預先設定好的參數，您可以套用
                                    </h5>
                                    <p onClick={() => {
                                        setValue('connect', 'ftp')
                                        setValue('gpu', '3')
                                        setValue('cpu','4')
                                        setValue('imagename', 'jupyter_notebook')
                                    }}>1.MATLAB</p>
                                    <p onClick={() => {
                                        setValue('connect', 'webtop')
                                        setValue('gpu', '4')
                                        setValue('cpu','5')
                                        setValue('memory', '6')
                                        setValue('imagename', 'webtop_itksnap')
                                    }}>2.CLC Genomics workbench</p>
                                    <p onClick={() => {
                                        setValue('connect', 'webide')
                                        setValue('gpu', '2')
                                        setValue('cpu','8')
                                        setValue('memory', '2')
                                        setValue('imagename', 'webtop_itksnap_mitkworkbench_3dslicer')
                                    }}>3.Partek flow</p>
                                    <p onClick={() => {
                                        setValue('connect', 'ssh')
                                        setValue('gpu', '1')
                                        setValue('cpu','10')
                                        setValue('memory', '4')
                                        setValue('imagename', 'webtop_itksnap_mitkworkbench_3dslicer')
                                    }}>4.RStudio</p>
                                    <p>5.Jupyter notebook</p>
                                    <p>6.TensorFlow</p>
                                    <p>7.AlphaFold2</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
               
                <button className="btn-upload">啟動</button>
            </div>
        </form>
    )
}

export default System
