import React from 'react'
import axios from 'axios'
import { useForm } from 'react-hook-form'


function Test() {
    const [user, setUser] = React.useState(null)
    const [targetUser, setTargetUser] = React.useState(null)
    const { register, handleSubmit } = useForm()

    const loginHandler = async (data) => {
  
        const response = await axios({
            method: 'POST',
            url: 'http://localhost:8000/api/login',
            data: {
                ...data
            },
            headers: { 'Content-Type': 'application/json' },
            // params: {
            //     y: 2,
            //     z: 3,
            //     brand: 'bmw'
            // }
        })
        console.log(1, response)
    }

    const adduserhandler = async (data) => {
        console.log(data)
        const response = await axios({
            method: 'POST',
            url: 'http://localhost:8000/api/users/add',
            data: {
                ...data
            }
        })
        console.log(response)
    }

    const filteruserhandler = async (data) => {
        const response = await axios({
            method: 'POST',
            url: 'http://localhost:8000/api/users/searchuser',
            data: {
                ...data,
            }
        })
        setTargetUser(response.data)
    }

    const userhandler = async () => {
        const response = await axios({
            method: 'GET',
            url: 'http://localhost:8000/api/users'
        })

        setUser(response.data)
    }

    return (
        <>
            <div>Test</div>

            
            <h3>Add user</h3>
            <form onSubmit={handleSubmit(adduserhandler)}>
                <label htmlFor="">email</label>
                <input type="text" {...register("username")}/>
                <label htmlFor="">password</label>
                <input type="text" {...register("password")}/>     
                <button>Click</button>           
            </form>
            <hr />
            <h2>selct User</h2>
            <form onSubmit={handleSubmit(filteruserhandler)}>
                <label htmlFor="">目標 user</label>
                <input type="text" {...register('username')}/>
                <button>Click</button>
            </form>
            <p>Target User: {JSON.stringify(targetUser)}</p>
            <hr />
            <h3>Login</h3>
            <form onSubmit={handleSubmit(loginHandler)}>
                <label htmlFor="">username</label>
                <input type="text" {...register("username")}/>
                <label htmlFor="">password</label>
                <input type="text" {...register("password")}/>
                <button>Click</button>
            </form>
            <hr />
            <h2>show USers</h2>
            <div>
                {JSON.stringify(user)}
            </div>
            <button onClick={userhandler}>show users</button>
            <hr />
            <button onClick={() => {
                axios({
                    method: 'GET',
                    url: 'http://localhost:8000/api/users/deleteall',
                })
            }}>USER Delete all</button>

        </>
    )
}

export default Test