import React from 'react'
import { Link } from 'react-router-dom'
import useUser from '../hook/useUser'
import { useNavigate } from 'react-router-dom'
import Cookies from 'js-cookie'
import { Turn as Hamburger } from 'hamburger-react'
import SingupLink from '../components/header/Singuplink'

function TheHeader() {
    const [isOpen, setOpen] = React.useState(false)
    const [menu, setMenu] = React.useState(false)
    const navigate = useNavigate()
    const isUser = useUser()
    return (
        <header>
            <div className="header-inner">
                    <div className="logo">
                        <Link to="/" onClick={() => {
                            setMenu(false)
                            setOpen()
                        }}>
                            Axtasy
                        </Link>
                    </div>
                    <div className="ham">
                        <Hamburger
                            toggled={isOpen}
                            toggle={setOpen}
                            onToggle={() => {
                                setMenu((p)=>!p)
                            }}
                        />
                    </div>
                    {/* small */}
                    <nav className={`menu-small ${menu ? 'menu-show': '' }`} >
                        <ul className="nav-list">
                            <li className="">
                                <Link to="/" onClick={() => {
                                    setMenu((p)=>!p)
                                    setOpen()
                                }}>
                                    首頁
                                </Link>
                            </li>
                            <li className="">
                                <Link to="/about" onClick={() => {
                                    setMenu((p)=>!p)
                                    setOpen()
                                }}>
                                    關於Axtasy                 
                                </Link>
                            </li>
                            {
                                isUser && (
                                    <li className="">
                                        <Link to="/system" onClick={() => {
                                            setMenu((p)=>!p)
                                            setOpen()
                                        }}>
                                            建立新任務
                                        </Link>
                                    </li>
                                )                          
                            }
                            {
                                isUser ? 
                                    JSON.parse(isUser)?.level === 1 && (
                                        <li className="">
                                            <Link to="/labsystem" onClick={() => {
                                                setMenu((p)=>!p)
                                                setOpen()
                                            }}>
                                                系統總覽
                                            </Link>
                                        </li>
                                    ) : 
                                    null
                            }
                            {
                                !isUser ? 
                                    <li className="">
                                        <Link to="/login" onClick={() => {setMenu((p)=>!p)}}>
                                            登入
                                        </Link>
                                    </li>
                                    :
                                    <li onClick={() => {
                                        Cookies.remove('user')
                                        navigate('/')
                                    }}>
                                        <span to="/" >
                                            登出
                                        </span>
                                    </li>
                            }
                            <li className="">
                                <SingupLink setMenu={setMenu}/>
                            </li>
                        </ul>
                       
                    </nav>

                    {/* big */}
                    <nav className={`menu`} >
                        <ul className="nav-list">
                            <li className="">
                                <Link to="/" onClick={() => {setMenu((p)=>!p)}}>
                                    首頁
                                </Link>
                            </li>
                            <li className="">
                                <Link to="/about" onClick={() => {setMenu((p)=>!p)}}>
                                    關於Axtasy                 
                                </Link>
                            </li>
                            {
                                isUser && (
                                    <li className="">
                                        <Link to="/system" onClick={() => {setMenu((p)=>!p)}}>
                                            建立新任務
                                        </Link>
                                    </li>
                                )                          
                            }
                            {
                                isUser ? 
                                    JSON.parse(isUser)?.level === 1 && (
                                        <li className="">
                                            <Link to="/labsystem" onClick={() => {setMenu((p)=>!p)}}>
                                                系統總覽
                                            </Link>
                                        </li>
                                    ) : 
                                    null
                            }
                            {
                                !isUser ? 
                                    <li className="">
                                        <Link to="/login" onClick={() => {setMenu((p)=>!p)}}>
                                            登入
                                        </Link>
                                    </li>
                                    :
                                    <li onClick={() => {
                                        Cookies.remove('user')
                                        navigate('/')
                                    }}>
                                        <span to="/" >
                                            登出
                                        </span>
                                    </li>
                            }
                            <li className="">
                                <SingupLink setMenu={setMenu}/>
                            </li>
                        </ul>
                    </nav>
                </div>
        </header>
    )
}

export default TheHeader