import React from 'react'

function Loading() {
    return (
        <div className="system-loading">
            <div className="loading-box">
                <div className="wrap">
                    <div className="loader-box">
                        <div class="loader"></div>
                    </div>
                    <h5>
                        啟動中
                    </h5>
                </div>
            </div>
        </div>
    )
}

export default Loading